<template>
  <div>
    <van-image class="imgs" :src="imgurl" />

    <van-row type="flex" justify="space-around" align="center" class="top2">
      <van-col class="txt mm" span="6">
        <van-button plain class="btnjian" size="mini" type="primary" round>
          <van-icon @click="back" name="arrow-left" />
        </van-button>
      </van-col>
      <van-col span="4" class="txt">
        <van-button plain class="btn" type="primary" size="mini" round @click="reg">注册</van-button>
      </van-col>
    </van-row>
    <van-row type="flex" justify="space-around" align="center" class="top3">
      <van-col span="24" class="zhong">Annasui安娜账号可登录Annasui香水</van-col>
    </van-row>
    <van-form @submit="onSubmit">
      <van-field v-model="ruleForm.username" name="用户名" label="用户名" placeholder="用户名/手机号码" />
      <van-field v-model="ruleForm.password" type="password" name="密码" label="密码" placeholder="密码" />
      <van-field v-model="ruleForm.vcode" center clearable label="验证码" placeholder="验证码">

        <template #button>
          <van-button @click.prevent="getyan" class="ma" ref="code" size="small" type="primary">9527</van-button>
        </template>

      </van-field>
      <div style="margin: 16px;">
        <van-checkbox v-model="checked" icon-size="14px">
          <em class="txt">七天免登录</em>
        </van-checkbox>
        <van-row type="flex" justify="space-between" align="center" class="top1 ju">
          <van-col class="txt mm zz" span="9">点击查看大图</van-col>
          <van-col span="4" class="txt zz" @click="huan">换一批</van-col>
        </van-row>
        <van-grid :border="false" :column-num="4">
          <van-grid-item class="imgg" v-for="item in goodslist" :key="item.sales_price">
            <img @click="showBig" class="igg" :src="item.img_url" />
          </van-grid-item>
        </van-grid>
        <van-button block color="#b0b0b0" type="info"  native-type="submit">登录</van-button>

        <van-row type="flex" justify="space-between" align="center" class="top1 wei">
          <van-col @click="regpas" class="txt mm zz" span="10">密码重置</van-col>
          <van-col @click="forgetpas" span="5" class="txt zz">忘记密码?</van-col>
        </van-row>
      </div>
    </van-form>
  </div>
</template>
<script>
import Vue from "vue";
import {
  ImagePreview,
  Image as VanImage,
  Col,
  Row,
  Icon,
  Form,
  Area,
  Grid,
  GridItem,
  Toast,
  Checkbox,
  CheckboxGroup,
} from "vant";
Vue.use(Checkbox);
Vue.use(CheckboxGroup);
Vue.use(Col);
Vue.use(Row);
Vue.use(Icon);
Vue.use(Form);
Vue.use(VanImage);
Vue.use(Area);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Toast);
export default {
  name: "Login",
  data() {
    return {
      checked: true,
      goodslist: [],
      huans:[],
      ruleForm: {
        username: "",
        password: "",
        vcode: "",
      },
      imgurl: "/img1/log.jpg",
    };
  },
  methods: {
    //重置密码
    regpas() {
      console.log(23);
      this.$router.push("/repas");
    },
    //忘记密码
    forgetpas() {
      console.log(23);
      this.$router.push("/forgetpas");
    },
    async getcode() {
      const result = await fetch(`http://47.112.131.23:2005/api/vcode`, {
        credentials: "include",
        withCredentials: true,
      }).then((res) => res.json());
      // console.log("请求的数据", result);

      if (result.code === 1) {
        this.$refs.code.innerHTML = result.data;
      }
    },
    getyan() {
      console.log(678);
      this.getcode();
    },
    async huan() {
      console.log(123);
      const {
        data: { data: goodslist },
      } = await this.$request.get("goods/huan", {
        params: {
          page:parseInt(Math.random()*10),
          total: 0,
        },
      });
      this.goodslist = goodslist;
      console.log(goodslist);
      this.huans=goodslist.map((item)=>{
        console.log(item.img_url);
        return item.img_url
      })
    },
    showBig() {
      ImagePreview({
        //局部注册
        // images: [
        //   "/img/7569fb57037806ee.jpg",
        //   "/img/4eb89d46f251b4a3.jpg",
        //   "/img/430defc76f25078e.jpg",
        //   "/img/4369ac9597ed6d9c.jpg",
        // ],
        images:this.huans,
        //设置closeable属性后，会在弹出层的右上角显示关闭图标，并且可以通过close-icon属性自定义图标，使用close-icon-position属性可以自定义图标位置。
        closeable: true,
      });
    },
    back() {
      window.history.go(-1);
    },
    //跳到注册
    reg() {
      this.$router.push({
        name: "Reg",
      });
    },
    async onSubmit(values) {
      console.log("submit", values);
      const username = this.ruleForm.username;
      const password = this.ruleForm.password;
      const vcode = this.ruleForm.vcode;
      const mdl = this.checked;

      const data = await this.$request.get("/log/", {
        params: {
          username,
          password,
          vcode,
          mdl,
        },
        credentials: "include",
        withCredentials: true,
      });
      console.log('返回的状态码',data.data.code);
      if (data.data.code === 1) {
        // this.ruleForm.username = "";
        // this.ruleForm.password = "";
        // this.$router.push({
        //   name: "Mine",
        // })恭喜你登录成功
        localStorage.setItem("currentUser", JSON.stringify(data.data.data));
        const { redirectTo = "/mine" } = this.$route.query;
        this.$router.replace(redirectTo);
      } else if (data.code === 10) {
        Toast.fail("亲,请正确填写验证码");
      } else {
        Toast.fail("登录失败!");
      }
    },
  },
  created() {
    this.getcode();
    this.huan();
  },
};
</script>
<style lang="scss">
.ju {
  margin-top: 5px;
}
.txt {
  font-size: 14px;
}
.ma {
  padding: 0;
}
.wei {
  margin-top: 10px;
}
.igg {
  display: block;
  width: 65px;
  height: 65px;
}
.zz {
  font-size: 14px;
}
.txt {
  color: #646566;
}

.top3 {
  position: absolute;
  top: 115px;
  left: 75px;
  .zhong {
    text-align: center;
    color: white;
    font-size: 14px;
  }
}
.imgs {
  position: relative;
}
.top2 {
  width: 100%;
  position: absolute;
  // background-color: chartreuse;
  top: 20px;
  .btnjian {
    height: 24px;
    width: 24px;
    border-radius: 100%;
    border: 1px solid white;
    border: 1px solid white;
    background-color: rgba(226, 220, 220, 0);
    .van-button__text {
      color: white;
    }
  }
  .btn {
    width: 60px;
    height: 24px;
    font-size: 12px;
    border: 1px solid white;
    background-color: rgba(226, 220, 220, 0);
    .van-button__text {
      color: white;
    }
  }
}
</style>